import LinkButton from "./LinkButton";

function LinkButtonList(props) {
  props.selectedRelease.data && console.log(props.selectedRelease.data);
  const linkButtonListProps = [
    ["SPOTIFY", "/img/spotify.png", props.selectedRelease.data.spotify.url],
    ["APPLE MUSIC", "/img/apple.png", props.selectedRelease.data.apple.url],
    ["DEEZER", "/img/deezer-small.png", props.selectedRelease.data.deezer.url],
    [
      "BANDCAMP",
      "/img/bandcamp-small.png",
      props.selectedRelease.data["buy-link"].url
    ],
    [
      "YOUTUBE",
      "/img/youtube-small.png",
      props.selectedRelease.data.youtube.url
    ]
  ];
  return (
    <div className="button-link-container">
      {linkButtonListProps.map(buttonProps => (
        <LinkButton
          streamingService={buttonProps[0]}
          streamingServiceLogo={buttonProps[1]}
          streamingServiceLink={buttonProps[2]}
        />
      ))}
      <style jsx>{`
        
        }
      `}</style>
    </div>
  );
}

export default LinkButtonList;
